.App {
  display: grid;
  grid-template-columns: 25% 50% 25%;
}

@media only screen and (max-width: 800px)  {
  .App {
    display: grid;
    grid-template-columns: 2% 96% 2%;
  }
}

.Container {
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.Resume {
  display: flex;
  flex-direction: column;
}

.Link {
  color: #551A8B;
  text-decoration-line: underline;
}

.Bros {
  text-align: center;
}

.Contacts {
  padding-bottom: 10px;
}